"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/Button";
import { Toggle } from "@/components/ui/Toggle";
import {
    getPreferences,
    setPreferences,
    setActiveSession,
    getActiveSession,
} from "@/lib/storage";

const DURATIONS = [
    { minutes: 25, label: "25 min", description: "Quick focus", isPro: false },
    { minutes: 50, label: "50 min", description: "Deep work", isPro: false },
    { minutes: 90, label: "90 min", description: "Flow state", isPro: true },
];

export default function HomePage() {
    const router = useRouter();
    const [selectedDuration, setSelectedDuration] = useState(25);
    const [brownNoise, setBrownNoise] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [mounted, setMounted] = useState(false);

    // Load preferences and check for existing session
    useEffect(() => {
        setMounted(true);
        const prefs = getPreferences();
        setBrownNoise(prefs.brownNoise);
        setSelectedDuration(prefs.lastDuration || 25);

        // If there's an active session, redirect to focus
        const session = getActiveSession();
        if (session) {
            router.push("/focus");
        }
    }, [router]);

    const handleLockIn = () => {
        setIsLoading(true);

        // Save preferences
        setPreferences({
            brownNoise,
            lastDuration: selectedDuration,
        });

        // Create new session
        setActiveSession({
            startTime: Date.now(),
            duration: selectedDuration,
            quitAttempts: 0,
            brownNoise,
        });

        // Navigate to focus mode
        router.push("/focus");
    };

    const handleBrownNoiseChange = (enabled: boolean) => {
        setBrownNoise(enabled);
        setPreferences({ brownNoise: enabled });
    };

    // Prevent hydration mismatch
    if (!mounted) {
        return (
            <div className="min-h-screen flex items-center justify-center">
                <div className="w-8 h-8 border-2 border-primary-500 border-t-transparent rounded-full animate-spin" />
            </div>
        );
    }

    return (
        <div className="min-h-screen flex flex-col">
            {/* Header */}
            <header className="p-6 flex items-center justify-between">
                <div className="flex items-center gap-2">
                    <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-primary-500 to-primary-700 flex items-center justify-center">
                        <span className="text-white font-bold text-sm">L</span>
                    </div>
                    <span className="font-semibold text-white">LockIn</span>
                </div>
                <nav className="flex items-center gap-4">
                    <a href="/pricing" className="text-sm text-surface-400 hover:text-white transition-colors">
                        Pricing
                    </a>
                </nav>
            </header>

            {/* Main content */}
            <main className="flex-1 flex flex-col items-center justify-center px-6 py-12">
                <div className="w-full max-w-xl mx-auto text-center space-y-12 animate-fade-in-up">
                    {/* Hero */}
                    <div className="space-y-4">
                        <h1 className="text-4xl md:text-5xl font-bold text-white leading-tight">
                            The simplest way to{" "}
                            <span className="text-gradient">force deep focus</span>
                        </h1>
                        <p className="text-lg text-surface-400">
                            No setup, no distractions.
                        </p>
                    </div>

                    {/* Duration presets */}
                    <div className="grid grid-cols-3 gap-4">
                        {DURATIONS.map((duration) => (
                            <button
                                key={duration.minutes}
                                onClick={() => !duration.isPro && setSelectedDuration(duration.minutes)}
                                className={`preset-btn ${selectedDuration === duration.minutes ? "selected" : ""
                                    } ${duration.isPro ? "pro opacity-60" : ""}`}
                                disabled={duration.isPro}
                            >
                                <div className="space-y-1">
                                    <span className="text-2xl md:text-3xl font-bold text-white">
                                        {duration.label}
                                    </span>
                                    <p className="text-xs text-surface-400">{duration.description}</p>
                                    {duration.isPro && (
                                        <span className="inline-block mt-2 px-2 py-0.5 text-xs font-medium text-amber-400 bg-amber-500/10 rounded-full border border-amber-500/30">
                                            Pro
                                        </span>
                                    )}
                                </div>
                            </button>
                        ))}
                    </div>

                    {/* Brown noise toggle */}
                    <div className="flex items-center justify-center gap-4">
                        <Toggle
                            enabled={brownNoise}
                            onChange={handleBrownNoiseChange}
                            label="Brown Noise"
                        />
                        <span className="text-xs text-surface-500">Helps with focus</span>
                    </div>

                    {/* Lock In button */}
                    <div>
                        <Button
                            variant="primary"
                            size="xl"
                            className="w-full max-w-xs mx-auto glow text-lg"
                            onClick={handleLockIn}
                            isLoading={isLoading}
                        >
                            Lock In
                        </Button>
                        <p className="mt-4 text-sm text-surface-500">
                            No sign-up required
                        </p>
                    </div>
                </div>
            </main>

            {/* Footer */}
            <footer className="p-6 text-center">
                <div className="flex items-center justify-center gap-6 text-xs text-surface-500">
                    <a href="/privacy" className="hover:text-surface-300 transition-colors">
                        Privacy
                    </a>
                    <a href="/terms" className="hover:text-surface-300 transition-colors">
                        Terms
                    </a>
                </div>
            </footer>
        </div>
    );
}
